
import {
  IonContent,
  IonHeader,
  IonPage,
  IonTitle,
  IonToolbar,
  IonLabel,
  IonItem,
  IonRefresher,
  IonRefresherContent,
  IonButton,
  IonButtons,
} from "@ionic/vue";
import { defineComponent } from "vue";
import useFirebaseFileUpload from "../hooks/firebase-file-upload";
import useFirebaseAuth from "../hooks/firebase-auth";
import { useRouter } from "vue-router";
import { trashBinOutline } from "ionicons/icons";

export default defineComponent({
  name: "AllUploads",
  components: {
    IonContent,
    IonHeader,
    IonPage,
    IonTitle,
    IonToolbar,
    IonLabel,
    IonItem,
    IonRefresher,
    IonRefresherContent,
    IonButton,
    IonButtons,
  },
  setup() {
    const { files, listFiles, deleteFile } = useFirebaseFileUpload();
    const { userData, logout } = useFirebaseAuth();
    const router = useRouter();

    const doDelete = async (item: any) => {
      await deleteFile(item.name);
      await listFiles();
    };
    /**
     *
     */
    const doLogout = async () => {
      await logout();
      router.options.scrollBehavior;
      router.replace({ name: "login", replace: true });
    };

    /**
     *
     */
    const doRefresh = async (event: any) => {
      await listFiles();
      console.log("complete..");
      await event.target.complete();
    };
    return {
      files,
      doRefresh,
      router,
      userData,
      doLogout,
      doDelete,
      trashBinOutline,
    };
  },
});
